.header__container {
    max-width: 100%;
    padding: 0 5px;
    border-bottom: 1px solid #BBC4D6;
}

@media screen and (min-width: 768px) {
    .header__container {
        border-bottom: 0;
        box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);
    }
}

.header__inner {
    max-width: 1170px;
    margin: auto;
}

@media screen and (min-width: 768px) {
    .header__inner {
        max-width: 768px;
    }
}

@media screen and (min-width: 1440px) {
    .header__inner {
        max-width: 1170px;
    }
}

.header__top {
    padding: 17px 21px 17px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

@media screen and (min-width: 768px) {
    .header__top {
        -ms-flex-align: center;
        align-items: center;
        padding: 0 21px;
        border-bottom: 0;
    }
}

.header__top-search {
    display: none;
}

@media screen and (min-width: 768px) {
    .header__top-search {
        min-width: 345px;
        display: block;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
        align-items: center;
        height: 40px;
        padding: 8px 0 8px 10px;
        border: 1px solid #EBEBF1;
        border-radius: 5px;
    }
}

@media screen and (min-width: 1440px) {
    .header__top-search {
        min-width: 765px;
    }
}

.header__user {
    display: none;
}

@media screen and (min-width: 768px) {
    .header__user {
        display: block;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
        justify-content: space-between;
        gap: 10px;
        -ms-flex-align: center;
        align-items: center;
    }
}

@media screen and (min-width: 768px) {
    .header__user-wrap {
        padding: 20px 0 20px 19px;
        border-left: 1px solid #EBEBF1;
    }
}

.header__user-name {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.01em;
    color: #768298;
}

.header__link-2 {
    -ms-flex-item-align: end;
    align-self: flex-end;
}

@media screen and (min-width: 768px) {
    .header__link-2 {
        display: none;
    }
}

.header__search-wrap {
    max-width: 385px;
    margin: auto;
    padding: 0 5px;
}

@media screen and (min-width: 768px) {
    .header__search-wrap {
        display: none;
    }
}

.header__search {
    max-width: 335px;
    padding-top: 8px;
    padding-left: 20px;
    padding-bottom: 8px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    gap: 5px;
}

.header__input {
    outline: none;
    border: none;
    width: 290px;
}

@media screen and (min-width: 1440px) {
    .header__input {
        width: 700px;
    }
}

.header__submit {
    width: 24px;
    height: 24px;
    border: none;
    outline: none;
    background: url(../../assets/images/search.svg);
    cursor: pointer;
}

.header ::-webkit-input-placeholder {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #BBC4D6;
}

.header ::-moz-placeholder {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #BBC4D6;
}

.header :-ms-input-placeholder {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #BBC4D6;
}

.header ::-ms-input-placeholder {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #BBC4D6;
}

.header ::placeholder {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #BBC4D6;
}

input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {
    display: none;
}

.partners__container {
    max-width: 385px;
    margin: auto;
    padding: 0 5px;
}

@media screen and (min-width: 768px) {
    .partners__container {
        max-width: 778px;
    }
}

@media screen and (min-width: 1440px) {
    .partners__container {
        max-width: 1180px;
    }
}

.partners__inner {
    padding: 70px 20px 73px;
    width: 100%;
}

@media screen and (min-width: 768px) {
    .partners__inner {
        padding: 70px 20px 0;
    }
}

.partners__title {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 160%;
    text-transform: uppercase;
    color: #121920;
    margin-bottom: 30px;
}

.partners__images {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
}

@media screen and (min-width: 768px) {
    .partners__images {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media screen and (min-width: 1440px) {
    .partners__images {
        grid-template-columns: repeat(4, 1fr);
    }
}

.partners__img {
    max-width: 335px;
    width: 100%;
}

.partners__img:hover {
    opacity: 0.5;
    outline: 1px solid black;
    border-radius: 5px;
    box-shadow: -3px 0px 40px 0px rgba(0, 0, 0, 0.5);
}
